.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.hidden-email {
  display: none;
}
.colorfix1 {
  color: white;
}
.colorfix1:hover {
  text-decoration: none;
  color: black;
}
.carousel-item > img { 
  width: 100%;
  height: 30vw;
}
@media (max-width: 1050px){
  .carousel-item > img {
    height: 35vw;
  }
}
@media (max-width: 850px){
  .carousel-item > img {
    height: 50vw;
  }
}
@media (max-width: 600px){
  .carousel-item > img {
    height: 65vw;
  }
}
@media (max-width: 400px){
  .carousel-item > img {
    height: 70vw;
  }
}
.carousel {
  width: 70%;
  display: flex;
  justify-items: center;
  justify-content: center;
  align-items: center;
  align-content: center;
}
